footer {
    background: #112;
    padding: 0px 40px;
    width: 100%;
    position: relative;
    height: 120px;
    right: 40px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 0px 10px 10px #112;
    margin-top: 20px;
    flex-shrink: 0;

    span {
        margin: 0px 5px;
        margin-top: 5px;
        color: #667;
        text-align: center;

        &:first-child {
            margin-top: 0px;
        }
    }
}